<template>
  <div>
    <!--   -->
    <div class="content">
      <h2 class="content-title">
        {{ searchNameResult ? `${searchNameResult}  ` : '   ' }}
      </h2>
      <p class="content-sub-title">    </p>

      <div class="d-flex justify-content-between align-items-center" style="color: red; font-weight: bold;">
        <p></p>
        <p>  "" </p>
      </div>

      <div class="d-flex justify-content-between align-items-start">
        <!--    -->
        <DefaultButton 
          v-if="isAdmin && searchNameResult"
          align="left"
          @click="openCreateModal"
        >
           
        </DefaultButton>

        <p></p>
        <!--   -->
        <div class="d-flex justify-content-between mb-3 search-body">
          <div>
            <input
              type="text"
              class="form-control input-search-name"
              v-model="searchName"
              placeholder=" "
              @keyup.enter="filterData"
            />
          </div>
          <DefaultButton @click="filterData">  </DefaultButton>
        </div>
      </div>

      <!--       -->
      <div v-if="!searchNameResult" class="d-flex justify-content-center mt-5">
        <img
          src="https://www.kpcnc.co.kr/src/img/main_slider1.png"
          alt="     "
          class="placeholder-image"
        />
      </div>

      <!-- Table View (PC +  ) -->
      <div class="table-container" v-if="!isMobile && searchNameResult">
        <table class="table mt-3" id="dataTable">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th> </th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="item in data"
              :key="item.fromDate"
              @click="openEditModal(item)"
            >
              <td>{{ item.fromDate }}</td>
              <td>{{ item.toDate }}</td>
              <td>{{ item.workType }}</td>
              <td>{{ item.performanceTitle }}</td>
              <td>{{ item.performance }}</td>
              <td :class="getStatusClass(item.status)">{{ item.status }}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <!-- Card Layout View (Visible on smaller screens) -->
      <div class="card-layout" v-if="isMobile && searchNameResult">
        <div class="card" v-for="item in data" :key="item.fromDate" @click="openEditModal(item)">
          <div class="card-header">
            <p class="card-title">{{ item.fromDate }} - {{ item.toDate }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>: </strong> {{ item.workType }}</p>
            <p class="card-text"><strong> : </strong> {{ item.performanceTitle }}</p>
            <p class="card-text"><strong>: </strong> {{ item.performance }}</p>
            <p class="card-text">
              <strong>: </strong>
              <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
          </div>
        </div>
      </div>

      <!-- (1)     -->
      <PerformanceManagementCreateModal
        :visible="createModalVisible"
        :formData="formData"
        @close="createModalVisible = false"
        @submit="submitPerformance"
      />

      <!-- (2)  /   -->
      <PerformanceManagementDeleteModifyModal
        :visible="editModalVisible"
        :editDataProp="editData"
        @close="editModalVisible = false"
        @save="submitEdit"
        @delete="removeItem"
      />

      <!--   AlertModal -->
      <AlertModal
        :isVisible="resultModalVisible"
        :disableBackgroundClose="true"
        title=" "
        confirmText=""
        @confirm="resultModalVisible = false"
      >
        <template #body>
          <p>   .</p>
        </template>
      </AlertModal>
    </div>
  </div>
</template>


<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';

import PerformanceManagementCreateModal from '@/components/common/PerformanceManagementCreateModal.vue';
import PerformanceManagementDeleteModifyModal from '@/components/common/PerformanceManagementDeleteModifyModal.vue';

import AlertModal from '@/components/common/modal/AlertModal.vue';
import DefaultButton from '@/components/common/button/DefaultButton.vue';

/**   */
const resultModalVisible = ref(false);

//  
const searchName = ref('');
const searchNameResult = ref('');
//  
const data = ref([]); //   (  )
const editData = ref({}); //    

//  
const userRole = ref('admin');
const isAdmin = computed(() => userRole.value === 'admin');

// " "  
const createModalVisible = ref(false);
const formData = ref({
  fromDate: '',
  toDate: '',
  workType: '',
  performanceTitle: '',
  performance: '',
  status: ' ',
});

// "/"  
const editModalVisible = ref(false);

/**   */
async function filterData() {
  if (!searchName.value.trim()) {
    alert('  .');
    data.value = [];
    searchNameResult.value = '';
    return;
  }
  // : ''  
  if (searchName.value === '') {
    data.value = [
      {
        fromDate: '2024-05-01',
        toDate: '2024-05-15',
        workType: '',
        performanceTitle: ' ',
        performance: '   ',
        status: '',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-07-15',
        toDate: '2024-07-30',
        workType: '',
        performanceTitle: '   ',
        performance: '    ',
        status: ' ',
      },
      {
        fromDate: '2024-08-30',
        toDate: '2024-09-10',
        workType: '',
        performanceTitle: ' ',
        performance: '   ',
        status: ' ',
      },
    ];
    searchNameResult.value = '';
  } else {
    //  
    try {
      const response = await axios.get('/api/performance', {
        params: { name: searchName.value },
      });
      data.value = response.data;
      searchNameResult.value = searchName.value;
    } catch (error) {
      console.error(' :', error);
      resultModalVisible.value = true;
    }
  }
}

/**   */
function getStatusClass(status) {
  if (status === ' ') return 'text-primary';
  if (status === '') return 'text-success';
  if (status === ' ') return 'text-warning';
  return '';
}

// (1)    
function openCreateModal() {
  //  
  formData.value = {
    fromDate: '',
    toDate: '',
    workType: '',
    performanceTitle: '',
    performance: '',
    status: ' ',
  };
  createModalVisible.value = true;
}
// (1')    ""
function submitPerformance(newForm) {
  console.log('Performance Submitted:', newForm);
  //   => data.value.push(newForm);
  createModalVisible.value = false;
}

// (2)   
function openEditModal(item) {
  editData.value = { ...item }; // 
  editModalVisible.value = true;
}
// (2')   ""
function submitEdit(updatedItem) {
  console.log('Performance Edited:', updatedItem);
  //   =>   
  editModalVisible.value = false;
}
// (2'')   ""
function removeItem(deletedItem) {
  console.log('Delete item:', deletedItem);
  data.value = data.value.filter((x) => x !== deletedItem);
  editModalVisible.value = false;
}

// 
const isMobile = ref(window.innerWidth <= 1150);

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});

</script>

<style scoped>
/*    */
.table-container {
  max-height: 400px; /*   */
  overflow-y: auto;  /*   */
}
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close {
  font-size: 1.5rem;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.performance-save-button {
  background-color: #375a7f;
  font-size: 0.875rem;
  padding: 0.4rem 0.75rem;
}

.bnt-search-name {
  background-color: #375a7f;
}

#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.placeholder-image {
  width: 500px;
  margin-bottom: 30px;
  height: auto;
  opacity: 0.8;
}

.align-items-start {
  margin-bottom: 10px;
}

@media (max-width: 1150px) {
  .performance-save-button {
    margin-bottom: 10px;
  }
}

@media (max-width: 768px) {
  .close {
    font-size: 1rem !important;
  }

  label {
    font-size: 0.85rem;
  }

  .placeholder-image {
    width: 400px;
    margin-bottom: 30px;
    margin-top: 50px;
    height: auto;
    opacity: 0.8;
  }

  .performance-save-button {
    margin-bottom: 0;
    font-size: 0.63rem;
    padding: 0.3rem 0.6rem;
  }

  .align-items-center {
    margin-bottom: 10px;
  }

  .text-muted,
  .card-text {
    font-size: 0.75rem;
    margin-bottom: 7px;
  }

  .input-search-name {
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
    width: 150px !important;
  }

  @media (max-width: 401px) {
    .placeholder-image {
      max-width: 100%;
      margin-bottom: 30px;
      margin-top: 50px;
      height: auto;
      opacity: 0.8;
    }
  }
}
</style>
