<template>
  <div class="table-container">
    <table v-if="showTable" class="table">
      <thead v-if="!noColumns">
        <tr>
          <th v-for="column in columns" :key="column.key">
            {{ column.label }}
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(item, rowIndex) in data"
          :key="rowIndex"
          @click="handleRowClick(item)"
        >
          <td
            v-for="column in columns"
            :key="column.key"
            :class="column.customClass 
                      ? column.customClass(item[column.key]) 
                      : ''"
          >
            {{ item[column.key] }}
          </td>
        </tr>
      </tbody>
      <tfoot>
        <tr>
          <td
            v-for="(footerItem, index) in footerData"
            :key="index"
          >
            {{ footerItem }}
          </td>
        </tr>
      </tfoot>
    </table>
  </div>
</template>

<script setup>
import { defineProps } from 'vue';

const props = defineProps({
  columns: {
    type: Array,
    required: false
  },
  data: {
    type: Array,
    required: true
  },
  footerData: {
    type: Array,
    required: false,
    default: () => []
  },
  calculateAmountPerPerson: {
    type: Function,
    required: false,
    default: (item) => item.amount
  },
  openSidebar: {
    type: Function,
    required: false
  },
  showTable: {
    type: Boolean,
    required: false,
    default: true
  },
  rowClick: {
    type: Function,
    required: false,
    default: null
  },
  noColumns: {
    type: Boolean,
    required: false,
    default: false
  },
});

const handleRowClick = (item) => {
  if (props.openSidebar) {
    props.openSidebar(item);
  }
  if (props.rowClick) {
    props.rowClick(item);
  }
};
</script>

<style scoped>
.text-red {
  color: red;
  font-weight: bold;
}
.text-black {
  color: black;
}
.text-blue {
  color: blue;
}
.text-green {
  color: green;
}
tbody tr:hover td {
  background-color: #e7f1ff !important;
}
</style>
