      <!--    -->
      <!--    ID : 7000  -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('receipt')" class="main-menu">
          <span class="arrow">{{ menus.receipt ? '' : '' }}</span>
          <span></span>
        </a>
        <transition @before-enter="beforeEnter" @enter="enter" @leave="leave">
          <div v-if="menus.receipt" class="submenu">
            
            <router-link to="/receipt/receipt-submission" @click="handleMenuClick('receipt')" class="submenu-item">  ()</router-link>
            <router-link to="/receipt/personal-receipt-history" @click="handleMenuClick('receipt')" class="submenu-item"> ()</router-link>
