            <router-link to="/receipt/receipt-request-overview" @click="handleMenuClick('receipt')" class="submenu-item">   ()</router-link>
            <router-link to="/receipt/receipt-personal-request-details" @click="handleMenuClick('receipt')" class="submenu-item">   ()</router-link>

            <router-link to="/receipt/receipt-search" @click="handleMenuClick('receipt')" class="submenu-item">  ()</router-link>
            <router-link to="/receipt/receipt-search-detail" @click="handleMenuClick('receipt')" class="submenu-item">   ()</router-link>

            <router-link to="/receipt/receipt-closure-overview" @click="handleMenuClick('receipt')" class="submenu-item">  (|)</router-link>
            
            <router-link to="/receipt/receipt-closure" @click="handleMenuClick('receipt')" class="submenu-item"> ()</router-link>
            
          </div>
        </transition>
      </div>

      <!--    -->
      <!--    ID : 9000  -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('user')" class="main-menu">
          <span class="arrow">{{ menus.user ? '' : '' }}</span>
          <span></span>
        </a>
        <transition @before-enter="beforeEnter" @enter="enter" @leave="leave">
          <div v-if="menus.user" class="submenu">
            <router-link to="/com/user-information" @click="handleMenuClick('user')" class="submenu-item"> ()</router-link>
            <router-link to="/com/" @click="handleMenuClick('user')" class="submenu-item"> ()</router-link>
          </div>
        </transition>
      </div>

      <!--   -->
      <!--   ID : 9000  -->
      <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('management')" class="main-menu">
          <span class="arrow">{{ menus.management ? '' : '' }}</span>
          <span></span>
        </a>
        <transition @before-enter="beforeEnter" @enter="enter" @leave="leave">
          <div v-if="menus.management" class="submenu">
            <router-link to="/com/user-management" @click="handleMenuClick('management')" class="submenu-item"> ()</router-link>
            <!-- <router-link to="/com/performance-history" @click="handleMenuClick('management')" class="submenu-item"> ()</router-link> -->
            <router-link to="/com/performance-management" @click="handleMenuClick('management')" class="submenu-item"> ()</router-link>
            <!--            -->
            <router-link to="/com/user-permissions" @click="handleMenuClick('management')" class="submenu-item"> ()</router-link>
            <router-link to="/com/dept-team-manager" @click="handleMenuClick('management')" class="submenu-item">/ ()</router-link>
          </div>
        </transition>
      </div>
      
      <!--     -->
      <!-- <div class="menu-item">
        <a href="#" @click.prevent="toggleMenu('organization')" class="main-menu">
          <span class="arrow">{{ menus.organization ? '' : '' }}</span>
          <span> </span>
        </a>
        <div v-if="menus.organization" class="submenu">
          <router-link to="/com/" @click="handleMenuClick('user')" class="submenu-item"> ()</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">()</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">()</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">()</router-link>
          <router-link to="/org/" @click="handleMenuClick('organization')" class="submenu-item">()</router-link>
        </div>
      </div> -->


      <div class="menu-item">
        <router-link to="/#" @click="closeSidebar" class="main-menu">
           ()
        </router-link>
        <router-link to="/join" @click="closeSidebar" class="main-menu">
          ()
        </router-link>
      </div>


    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';

//   
const menus = ref({
  receipt: false, //      
  user: false,    //      
  management: false,
  organization: false,    //      
});

//    
const isMobile = ref(window.innerWidth <= 768);
const isSidebarOpen = ref(false);

//    
const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 768;
};

//   
const toggleSidebar = () => {
  isSidebarOpen.value = !isSidebarOpen.value;

  //      
  if (isSidebarOpen.value) {
    document.querySelector('.mobile-menu-toggle').style.top = '85px';
  } else {
    document.querySelector('.mobile-menu-toggle').style.top = '90px';
  }
};

//      
const closeSidebar = () => {
  isSidebarOpen.value = false;
  const menuToggleButton = document.querySelector('.mobile-menu-toggle');
  if (menuToggleButton) {
    menuToggleButton.style.top = '90px';
  }
};

//   
const toggleMenu = (menu) => {
  Object.keys(menus.value).forEach(key => {
    if (key !== menu) {
      menus.value[key] = false;
    }
  });

  menus.value[menu] = !menus.value[menu];
};

//     
const handleMenuClick = () => {
  if (isMobile.value) {
    closeSidebar(); //    
  }
};

// JavaScript transition  
// --------------    --------------
const beforeEnter = (el) => {
  el.style.height = '0';
  el.style.opacity = '0';
};

const enter = (el, done) => {
  const height = el.scrollHeight;
  el.style.transition = 'all 0.3s ease';
  el.style.height = height + 'px';
  el.style.opacity = '1';
  el.addEventListener('transitionend', done);
};

const leave = (el, done) => {
  el.style.transition = 'all 0.3s ease';
  el.style.height = '0';
  el.style.opacity = '0';
  el.addEventListener('transitionend', done);
};
// ------------    END ------------

//      
window.addEventListener('resize', updateIsMobile);
</script>

<style scoped>
.sidebar {
  background-color: #375a7f;
  height: 100vh;
  padding-top: 30px;
  position: fixed;
  top: 65px;
  left: 0;
  width: 250px;
  transition: transform 0.3s ease;
  z-index: 999;
}

.sidebar.mobile-open {
  transform: translateX(0);
}

.menu-item {
  padding-left: 15px; /*    */
}

.main-menu {
  color: #fff;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 10px 0;
}

.submenu {
  padding-left: 12px; /*   */
  font-size: 0.8rem;
  /*     */
  /*   0      */
  /*         
           */
  overflow: hidden;
}

.submenu-item {
  color: #fff;
  text-decoration: none;
  display: block;
  padding: 6px 0 6px 10px; /*    */
}

.main-menu:hover,
.submenu-item:hover {
  background-color: #4f739b; /*          */
  color: #fff; /*      */
}

.arrow {
  margin-right: 5px;
  font-size: 0.5rem;
  font-weight: bold;
}

@media (max-width: 768px) {
  .mobile-menu-toggle {
    top: 90px;
    font-size: 1.5rem; /*   */
    margin-right: 10px;
    padding: 0 8px; /*   */
    border: none;
    background-color: white; /*    */
    border: 1px solid #ddd; /*    */
    border-radius: 3px; /*     */
    color: black;
    position: fixed;
    right: 10px;
    z-index: 1000;
    cursor: pointer;
    opacity: 0.5; /*   */
  }

  .mobile-menu-toggle.top-zero {
    background-color: transparent; /*      */
    border: none; /*      */
    opacity: 1; /*    */
    margin-right: 10px;
  }

  .mobile-menu-toggle .close-icon {
    color: white;
    font-size: 2rem;
  }

  .mobile-menu-toggle .menu-icon {
    color: black;
  }

  .sidebar {
    transform: translateX(-100%);
    width: 100%;
    height: 100vh;
    top: 65px;
    position: fixed;
    z-index: 999;
  }

  .sidebar.mobile-open {
    transform: translateX(0);
  }

  .sidebar a {
    padding: 10px 5px;
    text-align: left; /*    */
    border-right: none;
    font-size: 0.75rem;
  }

  .submenu a {
    padding-left: 20px; /*   */
    font-size: 0.7rem;
  }

  .content {
    margin-left: 0;
    padding: 20px;
    font-size: 0.875rem;
  }

  .content-title {
    font-size: 1.25rem;
    margin-bottom: 30px;
  }

  .submenu-item {
    padding: 6px 0 6px 10px !important; /*    */
  }
}
</style>
