// src/router/common.js
import NoticeIntegrated from '@/views/common/NoticeIntegrated.vue';
import UserInformation from '@/views/common/UserInformation.vue';
import UserManagement from '@/views/common/UserManagement.vue';
import DeptTeamManager from '@/views/common/DeptTeamManager.vue';
import PerformanceHistory from '@/views/common/PerformanceHistory.vue';
import PerformanceManagement from '@/views/common/PerformanceManagement.vue';
import UserPermissions from '@/views/common/UserPermissions.vue';

const vueTest = true;

const commonRoutes = [
  {
    //     
    path: 'notice-integrated',
    name: 'NoticeIntegrated',
    component: NoticeIntegrated,
    meta: { requiresAuth: !vueTest }, //   
  },
  {
    //     
    path: 'user-information',
    name: 'UserInformation',
    component: UserInformation,
    meta: { requiresAuth: !vueTest },
  },
  {
    //    
    path: 'user-management',
    name: 'UserManagement',
    component: UserManagement,
    meta: { requiresAuth: !vueTest },
  },
  {
    //    
    path: 'dept-team-manager',
    name: 'DeptTeamManager',
    component: DeptTeamManager,
    meta: { requiresAuth: !vueTest },
  },
  {
    //   
    path: 'performance-history',
    name: 'PerformanceHistory',
    component: PerformanceHistory,
    meta: { requiresAuth: !vueTest },
  },
  {
    //    
    path: 'performance-management',
    name: 'PerformanceManagement',
    component: PerformanceManagement,
    meta: { requiresAuth: !vueTest },
  },
  {
    //   
    path: 'user-permissions',
    name: 'UserPermissions',
    component: UserPermissions,
    meta: { requiresAuth: !vueTest },
  },
];

export default commonRoutes;
