// import { createRouter, createWebHistory } from 'vue-router';
import { createRouter, createWebHashHistory } from 'vue-router';
import receiptRoutes from './receipt';
import commonRoutes from './common';
import { isLoggedIn } from '@/utils/auth'; //    
import UserLogin from '@/views/common/UserLogin.vue';
import UserJoin from '@/views/common/UserJoin.vue';

const routes = [
  {
    path: '/',
    name: 'UserLogin',
    component: UserLogin,
    meta: { isLoginPage: true, requiresAuth: false }, //   
  },
  {
    path: '/join',
    name: 'UserJoin',
    component: UserJoin,
    meta: { isJoinPage: true, requiresAuth: false }, //   
  },
  {
    path: '/receipt',
    name: 'Receipt',
    children: receiptRoutes,
  },
  {
    path: '/com',
    name: 'Common',
    children: commonRoutes,
  },
];

const router = createRouter({
  // history: createWebHistory(),
  history: createWebHashHistory(), //  
  routes,
});

//    
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    //    
    if (!isLoggedIn()) {
      //      
      next({ name: 'UserLogin' });
    } else {
      //     
      next();
    }
  } else {
    //      
    next();
  }
});

export default router;
